import React, { useState } from 'react';
import './UploadPage.css'; // Correct path since it's in the same folder

const UploadPage = () => {
  const [selectedFiles, setSelectedFiles] = useState([]);
  const [previewURLs, setPreviewURLs] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [sortOrder, setSortOrder] = useState('asc');

  const itemsPerPage = 5;

  const handleFileChange = (e) => {
    const files = Array.from(e.target.files);
    const previews = files.map(file => URL.createObjectURL(file));
    setSelectedFiles(prev => [...prev, ...files]);
    setPreviewURLs(prev => [...prev, ...previews]);
  };

  const handleSort = () => {
    const sorted = [...selectedFiles].sort((a, b) => {
      const aName = a.name.toLowerCase();
      const bName = b.name.toLowerCase();
      return sortOrder === 'asc' ? aName.localeCompare(bName) : bName.localeCompare(aName);
    });

    setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    setSelectedFiles(sorted);

    // Recreate preview URLs
    setPreviewURLs(sorted.map(file => URL.createObjectURL(file)));
  };

  const totalPages = Math.ceil(selectedFiles.length / itemsPerPage);
  const paginatedFiles = selectedFiles.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );
  const paginatedPreviews = previewURLs.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  const goToPage = (page) => {
    if (page >= 1 && page <= totalPages) setCurrentPage(page);
  };

  return (
    <div className="upload-page">
      <h2>Upload Files (Image or PDF)</h2>
      <input type="file" onChange={handleFileChange} multiple />
      <div className="controls">
        <button onClick={handleSort}>
          Sort by Name ({sortOrder === 'asc' ? 'A  Z' : 'Z  A'})
        </button>
      </div>

      <div className="file-grid">
        {paginatedFiles.map((file, index) => (
          <div className="file-item" key={index}>
            {file.type.startsWith('image') ? (
              <img
                src={paginatedPreviews[index]}
                alt="preview"
                className="file-preview"
              />
            ) : (
              <iframe
                src={paginatedPreviews[index]}
                title={`file-${index}`}
                className="file-preview"
              />
            )}
            <p>{file.name}</p>
          </div>
        ))}
      </div>

      {totalPages > 1 && (
        <div className="pagination">
          <button onClick={() => goToPage(currentPage - 1)} disabled={currentPage === 1}>
             Prev
          </button>
          {Array.from({ length: totalPages }, (_, idx) => (
            <button
              key={idx}
              onClick={() => goToPage(idx + 1)}
              className={currentPage === idx + 1 ? 'active' : ''}
            >
              {idx + 1}
            </button>
          ))}
          <button onClick={() => goToPage(currentPage + 1)} disabled={currentPage === totalPages}>
            Next 
          </button>
        </div>
      )}
    </div>
  );
};

export default UploadPage;
