import React, { useState } from 'react';
import axios from 'axios';

export default function EmployeeForm() {
  const [form, setForm] = useState({ firstName: '', lastName: '', middleName: '', dob: '', department: '', salary: '', permAddress: '', currAddress: '' });
  const [file, setFile] = useState(null);

  const handleSubmit = async () => {
    const formData = new FormData();
    formData.append('data', new Blob([JSON.stringify(form)], { type: 'application/json' }));
    formData.append('file', file);

    try {
      await axios.post('http://localhost:8080/employees/add', formData);
      alert('Employee added');
    } catch (err) {
      alert('Error adding employee');
    }
  };

  return (
    <div>
      <h2>Add Employee</h2>
      <input placeholder="First Name" onChange={e => setForm({ ...form, firstName: e.target.value })} />
      <input placeholder="Middle Name" onChange={e => setForm({ ...form, middleName: e.target.value })} />
      <input placeholder="Last Name" onChange={e => setForm({ ...form, lastName: e.target.value })} />
      <input type="date" onChange={e => setForm({ ...form, dob: e.target.value })} />
      <select onChange={e => setForm({ ...form, department: e.target.value })}>
        <option>Engineering</option><option>Support</option><option>HR</option><option>Finance</option>
      </select>
      <input placeholder="Salary" onChange={e => setForm({ ...form, salary: e.target.value })} />
      <input placeholder="Permanent Address" onChange={e => setForm({ ...form, permAddress: e.target.value })} />
      <input placeholder="Current Address" onChange={e => setForm({ ...form, currAddress: e.target.value })} />
      <input type="file" accept="application/pdf" onChange={e => setFile(e.target.files[0])} />
      <button onClick={handleSubmit}>Submit</button>
    </div>
  );
}
