import React, { useState } from 'react';
import axios from 'axios';
import {
  TextField, Button, MenuItem, Box, Typography, InputLabel
} from '@mui/material';

export default function AddEmployee() {
  const [formData, setFormData] = useState({
    firstName: '',
    middleName: '',
    lastName: '',
    dob: '',
    department: '',
    salary: '',
    permanentAddress: '',
    currentAddress: '',
    idProof: null
  });

  const departments = ['Engineering', 'Support', 'HR', 'Finance'];

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file && file.type === 'application/pdf' && file.size >= 10240 && file.size <= 1048576) {
      setFormData(prev => ({ ...prev, idProof: file }));
    } else {
      alert("File must be a PDF between 10KB and 1MB.");
      e.target.value = null;
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const dobDate = new Date(formData.dob);
    const age = new Date().getFullYear() - dobDate.getFullYear();
    if (age < 18) {
      alert("Employee must be at least 18 years old.");
      return;
    }

    const data = new FormData();
    for (const key in formData) {
      data.append(key, formData[key]);
    }

    try {
      await axios.post("http://localhost:8080/employees", data, {
        headers: { "Content-Type": "multipart/form-data" }
      });
      alert("Employee added successfully!");
    } catch (err) {
      alert("Failed to add employee.");
      console.error(err);
    }
  };

  return (
    <Box sx={{ maxWidth: 600, mx: 'auto', mt: 5 }}>
      <Typography variant="h5" gutterBottom>Add New Employee</Typography>
      <form onSubmit={handleSubmit} encType="multipart/form-data">
        <TextField fullWidth label="First Name" name="firstName" onChange={handleChange} required margin="normal" />
        <TextField fullWidth label="Middle Name" name="middleName" onChange={handleChange} margin="normal" />
        <TextField fullWidth label="Last Name" name="lastName" onChange={handleChange} required margin="normal" />
        <TextField
          fullWidth
          type="date"
          name="dob"
          onChange={handleChange}
          label="Date of Birth"
          InputLabelProps={{ shrink: true }}
          required
          margin="normal"
        />
        <TextField
          fullWidth
          select
          name="department"
          label="Department"
          value={formData.department}
          onChange={handleChange}
          required
          margin="normal"
        >
          {departments.map(dep => (
            <MenuItem key={dep} value={dep}>{dep}</MenuItem>
          ))}
        </TextField>
        <TextField fullWidth label="Salary" name="salary" onChange={handleChange} required type="number" margin="normal" />
        <TextField fullWidth label="Permanent Address" name="permanentAddress" onChange={handleChange} required margin="normal" />
        <TextField fullWidth label="Current Address" name="currentAddress" onChange={handleChange} required margin="normal" />
        <InputLabel>Upload ID Proof (PDF Only)</InputLabel>
        <input type="file" accept="application/pdf" onChange={handleFileChange} required />
        <Button variant="contained" type="submit" sx={{ mt: 2 }}>Save</Button>
      </form>
    </Box>
  );
}
